<template>
  <div class="ctrl-pane">
    <div class="tabs">
      <div @click="clickTab(1)" :class="[active?'tabs-one-bg-active':'tabs-one-bg-normal','tabs-public-bg']">
        <div :class="[active?'tabs-one-active':'tabs-one-normal','tabs-public-item'] ">
          <span>{{$t('lang.tab1')}}</span>
        </div>
      </div>
      <div @click="clickTab(0)" :class="[!active?'tabs-two-bg-active':'tabs-two-bg-normal','tabs-public-bg']">
        <div :class="[!active?'tabs-two-active':'tabs-two-normal','tabs-public-item'] ">
          <span>{{$t('lang.tab2')}}</span>
        </div>
      </div>
    </div>
    <div class="ctrl-container">
      <div class="show-type">
        <p class="title">{{$t("lang.Visualization")}}</p>
        <div class="radio">
          <div class="radio-item" v-for="(item,index) in typeList" :key="index">
            <label role="radio" aria-checked="true" :class="[charType===index?'is-checked':'','el-radio'] ">
              <span :class="[charType===index?'is-checked':'','el-radio__input']">
                <span class="el-radio__inner"></span>
                <input @click="sendTypeToParent" type="radio" name="radio" aria-hidden="true" tabindex="-1" class="el-radio__original" v-model="charType" :value="item.type">
              </span>
              <span class="el-radio__label">{{$t(`lang.${item.text}`)}}</span>
            </label>
          </div>
        </div>
      </div>
      <div class="policy" v-if="!active">
        <p class="title">{{$t("lang.Policies")}}</p>
        <div class="radio">
          <div class="radio-item" v-for="(item,index) in policyList" :key="index" v-if="((areaType!=0) && (item.status==='city' || item.status==='cityProvince'))">
            <label role="radio" aria-checked="true" :class="[policyType===item.sname?'is-checked':'','el-radio'] ">
              <span :class="[policyType===item.sname?'is-checked':'','el-radio__input']">
                <span class="el-radio__inner"></span>
                <input type="radio" name="radio" aria-hidden="true" tabindex="-1" class="el-radio__original" v-model="policyType" :value="item.sname">
              </span>
              <span class="el-radio__label">{{$t(`lang.${item.text}`)}}</span>
            </label>
          </div>
          <div class="radio-item" v-for="(item,index) in policyList" :key="index" v-if="((areaType==0) && (item.status==='province' || item.status==='cityProvince'))">
            <label role="radio" aria-checked="true" :class="[policyType===item.sname?'is-checked':'','el-radio'] ">
              <span :class="[policyType===item.sname?'is-checked':'','el-radio__input']">
                <span class="el-radio__inner"></span>
                <input type="radio" name="radio" aria-hidden="true" tabindex="-1" class="el-radio__original" v-model="policyType" :value="item.sname">
              </span>
              <span class="el-radio__label">{{$t(`lang.${item.text}`)}}</span>
            </label>
          </div>
        </div>
      </div>
      <div class="area">
        <p class="title">{{$t("lang.SelectRegion")}}</p>
        <div class="radio">
          <div class="radio-item" v-for="(item,index) in areaList" :key="index">
            <label role="radio" aria-checked="true" :class="[areaType===index?'is-checked':'','el-radio'] ">
              <span :class="[areaType===index?'is-checked':'','el-radio__input']">
                <span class="el-radio__inner"></span>
                <input type="radio" name="radio" aria-hidden="true" tabindex="-1" class="el-radio__original" v-model="areaType" :value="item.type">
              </span>
              <span class="el-radio__label">{{$t(`lang.${item.text}`)}}</span>
            </label>
          </div>
        </div>
      </div>
      <div class="pollutant" v-if="active">
        <p class="title">{{$t("lang.SelectPollutant")}}</p>
        <div class="radio">
          <div class="radio-item" v-for="(item,index) in pollutantList" :key="index">
            <label role="radio" aria-checked="true" :class="[pollutantType===index?'is-checked':'','el-radio'] ">
              <span :class="[pollutantType===index?'is-checked':'','el-radio__input']">
                <span class="el-radio__inner"></span>
                <input type="radio" name="radio" aria-hidden="true" tabindex="-1" class="el-radio__original" v-model="pollutantType" :value="item.type">
              </span>
              <span class="el-radio__label" v-html="item.text"></span>
            </label>
          </div>
        </div>
      </div>
      <div class="year" v-if="charType===0">
        <p class="title">{{$t("lang.Year")}}</p>
        <div class="radio">
          <div class="radio-item" v-for="(item,index) in yearList" :key="index">
            <label role="radio" aria-checked="true" :class="[heatMapyear===index?'is-checked':'','el-radio'] ">
              <span :class="[heatMapyear===index?'is-checked':'','el-radio__input']">
                <span class="el-radio__inner"></span>
                <input type="radio" aria-hidden="true" tabindex="-1" class="el-radio__original" v-model="heatMapyear" :value="item.type">
              </span>
              <span class="el-radio__label">{{item.text}}</span>
            </label>
          </div>
        </div>
      </div>
      <div class="year" v-if="charType===1">
        <p class="title">{{$t("lang.Year")}}</p>
          <div class="checkbox" v-for="(item,index) in yearList" :key="index">
            <div class="checkbox-item">
              <label role="checkbox" aria-checked="true" :class="[barchartYear.indexOf(index)!==-1?'is-checked':'','el-checkbox'] ">
                <span aria-checked="mixed" :class="[barchartYear.indexOf(index)!==-1?'is-checked':'','el-checkbox__input']">
                  <span class="el-checkbox__inner"></span>
                  <input name="checkbox" type="checkbox" aria-hidden="true" class="el-checkbox__original" v-model="barchartYear" :value="item.type">
                </span>
                <span class="el-checkbox__label">{{item.text}}</span>
              </label>
            </div>
          </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    areaType: {
      get() {
        return this.$store.state.chartData.area
      },
      set(val) {
        this.$store.commit('UPDATE_AREA', val)
      }
    },
    active: {
      get() {
        return this.$store.state.chartData.tab
      },
      set(val) {
        this.$store.commit('UPDATE_TAB', val)
      }
    },
    heatMapyear: {
      get() {
        return this.$store.state.chartData.heatYear
      },
      set(val) {
        this.$store.commit('UPDATE_HEATYEAR', val)
      }
    },
    policyType: {
      get() {
        return this.$store.state.chartData.policy
      },
      set(val) {
        this.$store.commit('UPDATE_POLICY', val)
      }
    },
    pollutantType: {
      get() {
        return this.$store.state.chartData.pollutant
      },
      set(val) {
        this.$store.commit('UPDATE_POLLUTANT', val)
      }
    },
    barchartYear: {
      get() {
        return this.$store.state.chartData.barchartYear
      },
      set(val) {
        this.$store.commit('UPDATE_BARCHARTESR', val)
      }
    },

  },
  data: () => {
    return {
      charType: 0,
      // areaType: 0,
      // heatMapyear: 0,
      // yearType: 0,
      // policyType: 0,
      // pollutantType: 0,
      // active: 1,
      typeList: [{ text: "map", type: 0 }, { text: "BarChart", type: 1 }],
      areaList: [
        { text: "Province", type: 0 },
        { text: "Cities", type: 1 },
        { text: "KeyCities", type: 2 },
        { text: "BTH", type: 3 },
        { text: "YRD", type: 4 },
        { text: "PRD", type: 5 }
      ],
      pollutantList: [
        { text: "PM<sub>2.5</sub>", type: 0 },
        { text: "NO₂", type: 1 },
        { text: "PM<sub>10</sub>", type: 2 },
        { text: "O₃", type: 3 },
        { text: "SO₂", type: 4 },
        { text: "CO", type: 5 }
      ],
      yearList: [
        { text: "2013", type: 0 },
        { text: "2014", type: 1 },
        { text: "2015", type: 2 },
        { text: "2016", type: 3 },
        { text: "2017", type: 4 },
        // { text: "2018", type: 5 }
      ],
      policyList: [
        { text: "steel", sname: "GT", status:'city', type: 0 },//淘汰钢铁产能 
        { text: "cement", sname: "SN", status:'city', type: 1 },// 淘汰水泥产能
        { text: "firedBoilers",status:'city', sname: "GL", type: 2 },// 燃煤锅炉治理情况
        { text: "yellowLabel",status:'cityProvince', sname: "HBC", type: 3 },// 淘汰黄标车
        // { text: "enterprise", sname: "QY", type: 4 },// 企业淘汰
        { text: "CoalConsumption", status:'province',sname:"CC", type: 5 },// 煤炭消费量
        { text: "PowerGeneration",status:'province', sname:"PG", type: 6 },//落后火电机组淘汰量
        { text: "EmissionInstallation",status:'province',sname:"EI", type: 7 },//燃煤电厂超低排放改造量
        { text: "energyVehicle",status:'city',sname:"EV", type: 8 },//新能源车推广量
        { text: "OilVapor",status:'city',sname:"OV", type: 9 },//加油站油气回收改造量
      ]
    };
  },
  mounted() {},
  watch: {
    charType(charType) {
      // this.radioValue = charType;
    },
    areaType(areaType) {
      // this.radioValue = areaType;
    }
  },
  methods: {
    sendTypeToParent(){ // 展示形式
      this.$nextTick(() => {
        this.$emit("listenOperationType",this.charType)
      })
    },
    // changeHeatMapYear(){ // 热力图
    //   this.$nextTick(() => {
    //     this.$emit("listenHeatYear", this.yearList[this.heatMapyear])
    //   })
    // },
    clickTab(tab) {
      if (this.active == tab) return;
      this.active = tab;
    }
  }
};
</script>

<style lang="scss" scoped>
.title {
  font-size: 20px;
  color: #4a4a4a;
  margin-bottom: 1rem;
}
.ctrl-pane {
  // position:absolute;
  // top: 80px;
  // left: 20px;
  width: 250px;
  margin: 80px 0 0 20px;
  background-color: #fff;
  box-shadow: 0px 0px 8px 1px rgba(0, 0, 0, 0.3);
  border-radius: 8px;
  .tabs {
    display: flex;
    background-color: #fff;
    border-radius: 8px 8px 0 0;
    height: 40px;
    .tabs-public-bg {
      width: 50%;
      .tabs-public-item {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        width: 100%;
        font-size: 18px;
        cursor: pointer;
        user-select: none;
      }
    }
    .tabs-one-bg-normal {
      .tabs-one-normal {
        border-radius: 8px 0px 12px 0;
        background-color: #e0e0e0;
        color: #9b9b9b;
      }
    }
    .tabs-two-bg-active {
      background-color: #e0e0e0;
      .tabs-two-active {
        border-radius: 12px 8px 0 0;
        background-color: #fff;
        color: #32adfe;
      }
    }
    .tabs-one-bg-active {
      background-color: #e0e0e0;
      .tabs-one-active {
        border-radius: 8px 12px 0 0;
        background-color: #fff;
        color: #32adfe;
      }
    }
    .tabs-two-bg-normal {
      .tabs-two-normal {
        border-radius: 0 8px 0 12px;
        background-color: #e0e0e0;
        color: #9b9b9b;
      }
    }
  }
  .ctrl-container {
    .show-type,
    .area,
    .pollutant,
    .year {
      padding: 0.5rem 10px;

      .radio {
        .radio-item {
          margin: 5px 0;
          display: inline-block;
          width: 110px;
        }
      }
    }
    .policy,
    .area,
    .pollutant,
    .year {
      border-top: 1px solid #ccc;
    }
    .policy {
      padding: 0.5rem 10px;
      .radio {
        .radio-item {
          margin: 5px 0;
          word-wrap: break-word;
        }
      }
    }
  }
}
.is-checked {
  background-color: #409eff;
  .el-radio__inner {
    border: none !important;
  }
  .el-radio__inner:after {
    background-color: #409eff;
  }
}
.el-radio {
  display: inline-flex;
  align-items: center;
  max-width: 100%;
  min-height: 24px;
  border-radius: 12px;
  padding-left: 5px;
  padding-right: 10px;
  color: #606266;
  font-weight: 500;
  line-height: 1;
  position: relative;
  cursor: pointer;
  // white-space: nowrap;
  outline: none;
  font-size: 14px;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  .el-radio__input {
    display: flex;
    white-space: nowrap;
    // cursor: pointer;
    // outline: none;
    // display: inline-block;
    // line-height: 1;
    // position: relative;
    // vertical-align: middle;
    .el-radio__inner {
      border: 1px solid #dcdfe6;
      border-radius: 100%;
      width: 14px;
      height: 14px;
      background-color: #fff;
      position: relative;
      cursor: pointer;
      display: inline-block;
      box-sizing: border-box;
    }
  }
}
.el-radio__input.is-checked .el-radio__inner:after {
  transform: translate(-50%, -50%) scale(1);
}
.el-radio__inner:after {
  width: 4px;
  height: 4px;
  border-radius: 100%;
  background-color: #fff;
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) scale(0);
  transition: transform 0.15s ease-in;
}
.el-radio__original {
  opacity: 0;
  outline: none;
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0;
}
.el-radio__input.is-checked + .el-radio__label {
  color: #fff;
}
.el-radio__label {
  font-size: 14px;
  padding-left: 5px;
}
.checkbox {
  display: inline-flex;
  .checkbox-item {
    margin: 5px 0;
    display: inline-block;
    width: 110px;
  }
}
.el-checkbox {
  color: #606266;
  font-weight: 500;
  font-size: 14px;
  position: relative;
  cursor: pointer;
  display: inline-block;
  white-space: nowrap;
  user-select: none;
  border-radius: 6px;
  padding: 2px 6px;
  .el-checkbox__input {
    white-space: nowrap;
    cursor: pointer;
    outline: none;
    display: inline-block;
    line-height: 1;
    position: relative;
    vertical-align: middle;
  }
}
.el-checkbox__input.is-checked .el-checkbox__inner {
  background-color: #fff;
  background-image: url("../assets/images/checkBox_close.png");
  background-size: 80%;
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  width: 16px;
  height: 16px;
}
.el-checkbox__inner {
  display: inline-block;
  position: relative;
  border: 2px solid #9b9b9b;
  border-radius: 5px;
  box-sizing: border-box;
  width: 18px;
  height: 18px;
  background-color: #fff;
  z-index: 1;
  transition: border-color 0.25s cubic-bezier(0.71, -0.46, 0.29, 1.46),
    background-color 0.25s cubic-bezier(0.71, -0.46, 0.29, 1.46);
  background-image: url("../assets/images/checkBox_add.png");
  background-size: 100%;
}
.el-checkbox__inner:after {
  box-sizing: content-box;
  content: "";
  border: 1px solid #fff;
  border-left: 0;
  border-top: 0;
  height: 7px;
  left: 4px;
  position: absolute;
  top: 1px;
  transform: rotate(45deg) scaleY(0);
  width: 3px;
  transition: transform 0.15s ease-in 0.05s;
  transform-origin: center;
}
.el-checkbox__original {
  opacity: 0;
  outline: none;
  position: absolute;
  margin: 0;
  width: 0;
  height: 0;
  z-index: -1;
}

.el-checkbox__input.is-checked + .el-checkbox__label {
  color: #fff;
}
// .is-checked {
//   box-sizing: border-box;
//   background-color: #32adfe;
// }
.el-checkbox__label {
  display: inline-block;
  padding-left: 10px;
  line-height: 19px;
  font-size: 14px;
}
</style>
